import { createSlice } from '@reduxjs/toolkit';

//  filter  redux-
const initialState = {
  filter: '',
};

// filterSlice
export const filterSlice = createSlice({
  name: 'filter',
  initialState,
  reducers: {
    changeFilter: (state, action) => {
      state.filter = action.payload;
    },
  },
});

export const { changeFilter } = filterSlice.actions; //  actions   
export const filterReducer = filterSlice.reducer; //  filterReducer   
